<template>
    <div>
        <!-- <div class="title" style="text-align:center;"></div> -->
        <div id="myChart5" :style="{width:'100%',height:'300px',marginTop:'30px'}"></div>
    </div>
</template>
<script>
export default {
    data(){
        return {

        }
    },
    mounted(){
        this.drawLine();
        // this.init()
    },
    methods:{
        drawLine(){
            let myChart = this.$echarts.init(document.getElementById('myChart5'))
            myChart.setOption({
                title:{
                    text:'',
                    left:'center',
                    textStyle:{
                        fontSize:16,
                        fontWeight:'normal'
                    }
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer : { // 
                        type : 'shadow' // 'line' | 'shadow'
                    }
                },
                grid:{
                    top:'20%',
                    bottom:'20%',
                    left:'6%',
                    right:'10%',
                    // height: '250',
                },
                legend: [
                    {
           bottom: 0,
                        // right: "24%",
          textStyle: {
           color: "black",
           fontSize: 12,
           fontFamily: ""
          },
          itemWidth: 20,
          itemHeight: 7,
          data:[{
           name: "",
           icon: "rect"
          },{
           name: "",
           icon: "line"
          }]
         }
                ],
                xAxis: [
                        {
                            type: 'category',
                            data:["","","",""],
                            axisTick:false,
                            axisLabel: {
				                interval:0,//
				                rotate:0,//-30    
                            }
                        }
                    ],
                    yAxis: [
                        {
                            axisLine: {show:false},
                            axisTick: {show:false},
                            // max:20,
                            type: 'value',
                            // name: '/ml',
                            min: 0,
                            max: 6,
                            interval: 2,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        },
                        {
                            axisLine: {show:false},//y
                            axisTick: {show:false},//y
                            max:20,
                            type: 'value',
                            // name: '/ml',
                            min: 0,
                            max: 15,
                            interval: 5,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        }
                    ],
                series: [
                        {
                            name:'',
                            type:'bar',
                            yAxisIndex:0,
                            barWidth: '20',
                            itemStyle: {
                                normal: {
                                    color: '#5B9BD5',
                                    /**/
                                    // label: {
                                    //     show: true,
                                    //     position: 'top',
                                    //     // formatter: '{b}\n{c}'
                                    // }
                                }
                            },
                            data:["5","3","4","2"]
                        },
                        {
                            name:'',
                            type:'line',
                            yAxisIndex:1,
                            showSymbol:false,//
                            symbol:'none',//
                            itemStyle : {  /**/
                                normal : {
                                    color:'#ED833C'
                                }
                            },
                            data:["10","4","7","3"]
                        }

                    ]
            });
            window.addEventListener("resize", () => { myChart.resize();});
        }
    }
}
</script>
<style lang="scss" scoped>
    #myChart5{
        width: 100%;
    }
</style>
 



