<template>
    <div class="zaijian">
        <el-row>
        <el-col :span="24" class="zai-title"><div class="grid-content bg-purple-dark">
            <el-col :span="10"><div class="grid-content zai-title-title bg-purple"></div></el-col>
        </div></el-col>
        </el-row>
        <el-row class="indexof">
            <el-col :xs="24" :sm="12" :md="4"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="">
                        <el-select v-model="form.region" placeholder="--" style="width:60%">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple-light">
                <el-form :model="form" label-width="80px" size="mini">
                    <el-form-item label="">
                        <el-input v-model="form.user" style="width:60%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="4"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="">
                        <el-select v-model="form.building" style="width:60%">
                        <el-option 
                        v-for="i in form.build"
                        :key="i.value"
                        :label="i.label"
                        :value="i.value"
                        ></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="">
                        <el-select v-model="form.items" style="width:60%">
                        <el-option 
                        v-for="i in form.item"
                        :key="i.value"
                        :label="i.label"
                        :value="i.value"
                        ></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="4"><div class="grid-content bg-purple-light">
                <el-form label-width="80px" size="mini">
                    <el-form-item>
                        <el-button style="background:#F2F2F2;border:1px solid #CCCFD2;outline:none" @click="search"></el-button>
                    </el-form-item>
                </el-form>
            </div></el-col>
        </el-row>
        <el-row>
        <el-col :span="24"><div class="grid-content tables bg-purple-dark">
            <el-table
                :data="tableData"
                border
                style="width: 100%"
                :header-cell-style="{background:'#E6E6E6',color:'#000'}">
                <el-table-column
                fixed
                label=""
                align="center"
                width="50">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small" style="outline:none;"></el-button>
                </template>
                </el-table-column>
                <el-table-column
                fixed
                prop="date"
                label=""
                align="center"
                width="50">
                </el-table-column>
                <el-table-column
                prop="name"
                label=""
                align="center"
                width="120">
                </el-table-column>
                <el-table-column
                prop="work"
                label=""
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="project"
                label=""
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="item"
                label=""
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="province"
                label=""
                align="center"
                width="70">
                </el-table-column>
                <el-table-column
                prop="data1"
                label=""
                align="center"
                width="100">
                </el-table-column>
                <el-table-column
                prop="money"
                label=""
                align="center"
                width="80">
                </el-table-column>
                <el-table-column
                prop="unit"
                label=""
                align="center"
                width="80">
                </el-table-column>
                <el-table-column
                prop="manager"
                label=""
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="tel"
                label=""
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="state"
                label=""
                align="center"
                width="100">
                </el-table-column>
                <el-table-column
                prop="win"
                label=""
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="punish"
                label=""
                align="center"
                min-width="90">
                </el-table-column>
            </el-table>
        </div></el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form:{
                user:'',
                region: [{
                    value: '',
                    label: ''
                },{
                    value: '',
                    label: ''
                }],
                building:'',
                build:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                }],
                items:'',
                item:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                }]
            },
            tableData: [{
                date: '1',
                name: '',
                project: '',
                work: '',
                item: '',
                province: '',
                data1:'2016-05-03',
                money:'20000',
                unit:'',
                manager:'',
                tel:'18601933060',
                state:'',
                win:'',
                punish:''
                }, {
                date: '2',
                name: '',
                project: '',
                work: '',
                item: '',
                province: '',
                data1:'2016-06-05',
                money:'10020',
                unit:'',
                manager:'',
                tel:'18601933060',
                state:'',
                win:'',
                punish:''
                }, {
                date: '3',
                name: '',
                project: '',
                work: '',
                item: '',
                province: '',
                data1:'2017-06-03',
                money:'20000',
                unit:'',
                manager:'',
                tel:'18134214803',
                state:'',
                win:'',
                punish:''
                }]  
        }
    },
    methods:{
        search(){
            console.log(1)
        },
        handleClick(row) {
            this.$router.push({path:'/date',query:{date:row}})
        }
    }
}
</script>
<style lang="scss">
.zaijian{
    .zai-title{
        background: #9AD5F8;
        .zai-title-title{
            background: #fff;
            margin-left: 5px;
            text-align: center;
            font-size: 12px;
            line-height: 26px;
        }
    }
    .indexof{
        background: #E6F3FF;
        padding-bottom: 8px;
        .el-form-item{
            margin-bottom: 0 !important;
        }
        .el-form-item__label{
            font-size: 10px !important;
            margin-bottom: 0 !important;
        }
        .el-col{
            margin-top: 8px;
        }
    }
    .tables{
        .el-table td,.el-table th{
            padding:6px 0 !important;
        }
        .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed{
            border-right: 1px solid #ccc !important;
        }
        .el-table td, .el-table th.is-leaf{
            border-bottom: 1px solid #ccc;
        }
        .el-button--small, .el-button--small.is-round {
            padding:0 0;
        }
    }
}
</style>



