        <el-row>
        <el-col :span="24"><div class="grid-content bg-purple-dark">
            <el-tabs type="border-card">
            <el-tab-pane label="">
            <el-row>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label=":">
                        <el-input v-model="form.name" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label=":">
                    <el-select v-model="form.value" style="width:70%">
                    <el-option
                    v-for="item in form.project"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                    </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label=":">
                        <el-select v-model="form.value1" style="width:70%">
                        <el-option
                        v-for="item in form.work"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label=":">
                        <el-select v-model="form.index" style="width:70%">
                        <el-option
                        v-for="item in form.items"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label=":">
                        <el-input v-model="form.place" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label=":">
                        <el-select v-model="form.city" style="width:70%">
                        <el-option
                        v-for="item in form.province"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label=":">
                        <el-input v-model="form.owner" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
