                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="">
                        <el-date-picker type="date" placeholder="" v-model="form.date2" style="width: 70%;"></el-date-picker>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label=":">
                        <el-select v-model="form.classes" style="width:70%;">
                            <el-option
                            v-for="item in form.class"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
