                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="">
                        <el-input type="textarea" v-model="form.common" style="width:90%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            </el-row>
            </el-tab-pane>
            <el-tab-pane label="">{{form.advise}}</el-tab-pane>
            </el-tabs>
