<template>
    <div>
        <!-- <div class="title" style="text-align:center"></div> -->
        <div id="myChart2" :style="{width:'100%',height:'300px',marginTop:'30px'}"></div>
    </div>
</template>
<script>
import $ from 'jquery'
export default {
    data(){
        return {
        //    as:this.areass
        }
    },
    // props:['areass'],
    mounted(){
        this.drawLine();
        // console.log(this.months)
    },
    methods:{
        drawLine(){
            // domecharts
            let myChart = this.$echarts.init(document.getElementById('myChart2'))
            // 
            myChart.setOption({
                title:{
                    text:'',
                    left:'center',
                    textStyle:{
                        fontSize:16,
                        fontWeight:'normal'
                    }
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross',
                        crossStyle: {
                            color: '#999'
                        }
                    }
                },
                 grid:{
                    top:'20%',
                    bottom:'20%',
                    left:'13%',
                    right:'13%',
                    // height: '250',
                },
                legend: [
                    {
           bottom: 0,
                        // right: "24%",
          textStyle: {
           color: "black",
           fontSize: 12,
           fontFamily: ""
          },
          itemWidth: 20,
          itemHeight: 7,
          data:[
                        {
           name: "",
           icon: "rect"
          },{
           name: "",
           icon: "rect"
          },{
           name: "",
           icon: "line"
          }]
         }
                ],
                xAxis: [
                    {
                        axisTick:false,
                        type: 'category',
                        data: ['','','',''],
                        axisPointer: {
                            type: 'shadow'
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#ccc',//
                            }
                        },
                        axisLabel: {
                            textStyle: {
                                fontSize:12,
                                color:'#000',
                            }
                        }
                    }
                ],
                yAxis: [
                    {
                        type: 'value',
                        // name: '',
                        axisTick:false,
                        axisLine: {show:false},
                        min: 0,
                        max: 1000,
                        interval: 500,
                        axisLabel: {
                            // formatter: '{value} ml'
                        }
                    },
                    {
                        type: 'value',
                        // name: '',
                        axisTick:false,
                        axisLine: {show:false},
                        splitLine:{show:false},//
                        min: 0,
                        max: 100,
                        interval: 50,
                        axisLabel: {
                            // formatter: '{value} C'
                        }
                    }
                ],
                series: [
                    {
                        name:'',
                        type:'bar',
                        barWidth: '20',
                        yAxisIndex:0,
                        itemStyle : {  /**/
                            normal : {
                                color:'#70AD47'
                            }
                        },
                        data:[500, 395, 180, 420]
                    },
                    {
                        name:'',
                        type:'bar',
                        barWidth: '20',
                        yAxisIndex:0,
                        itemStyle : {  /**/
                            normal : {
                                color:'#4472C4'
                            }
                        },
                        data:[323, 313, 150, 380]
                    },
                    {
                        name:'',
                        type:'line',
                        yAxisIndex:1,
                        showSymbol:false,//
                        symbol:'none',//
                        itemStyle : {  /**/
                            normal : {
                                color:'#ED833C'
                            }
                        },
                        // yAxisIndex: 1,
                        data:[60, 80, 30, 70]
                    }
                ]
            });
            window.addEventListener("resize", () => { myChart.resize();});
        }
    }
}
</script>
<style scoped>

</style>



