<template>
    <div style="text-align:center;">
        <!-- <div class="title"></div> -->
        <div id="mapChart" style="width:100%;height:300px;margin-top:30px;"></div>
    </div>
</template>
<script>
import echarts from "echarts"
import "../../../node_modules/echarts/map/js/china.js";
export default {
  mounted() {
    // this.init();
      let myChart = echarts.init(document.getElementById('mapChart'));
      var datas=[
              {name: '', value: '',selected:true},
              {name: '', value: '',selected:true},
              {name: '', value: '',selected:true},
              {name: '', value: '',selected:true}
          ]
      let option = {
        title:{
          text:'',
          left:'left',
          textStyle:{
            fontSize:14
          }
        },
        tooltip: {
            trigger: 'item',
            formatter(params){
              var info = params.name;
              var res = '';
              for(var i = 0;i<datas.length;i++){
                    if(datas[i].name == info){
                        res = `<div class="select"><p>`+info+`</p><p>`+datas[i].value+`</p></div>`;
                        break;
                    }
                }
              return res; 
            },
            backgroundColor:"#fff",//
            textStyle:{color:"#000"} //
        },
        series:[{
          type: 'map',
          mapType: 'china', // 
          mapLocation: {
            x: 'center'
          },
          //selectedMode : 'multiple',//
          zoom: 1.2, // 
          label: {
            normal:{
              show:false
            },
            emphasis:{
              show:false
            }
          },
          itemStyle: { //  
            normal:{
              borderColor: 'rgb(169, 223, 248)',
              borderWidth:2,
              areaColor: "#83C9FD",
            },
            emphasis: {                          //
              areaColor: 'orange',  //
              borderWidth:0     //
            },
          },
          data:datas
        }]
      };
      myChart.setOption(option);
      window.addEventListener("resize", () => { myChart.resize();});
    },
};
</script>
<style lang="scss">
  .select{
    border: 1px solid #ccc;
    box-shadow: 2px 2px 4px 2px #ccc;
  }
</style>



