<template>
    <div>
        <!-- <div class="title" style="text-align:center"></div> -->
        <div id="myChart1" :style="{width:'100%',height:'300px',marginTop:'30px'}"></div>
    </div>
</template>
<script>
import $ from 'jquery'
export default {
    data(){
        return {
        //    ms:this.months,
        //    js:this.jindus,
        //    fs:this.finishs
        }
    },
    // props:['months','finishs','jindus'],
    mounted(){
        this.drawLine(); 
        // console.log(this.months)
    },
    methods:{
        drawLine(){
            // domecharts
            let myChart = this.$echarts.init(document.getElementById('myChart1'))
            // 
            myChart.setOption({
                title:{
                    text:'',
                    left:'center',
                    textStyle:{
                        fontSize:16,
                        fontWeight:'normal'
                    }
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer : { // 
                        type : 'shadow' // 'line' | 'shadow'
                    }
                },
                grid:{
                    top:'20%',
                    bottom:'20%',
                    left:'10%',
                    right:'10%',
                    // height: '250',
                },
                legend: [
                    {
           bottom: 0,
                        // right: "24%",
          textStyle: {
           color: "black",
           fontSize: 12,
           fontFamily: ""
          },
          itemWidth: 27,
          itemHeight: 10,
          data:[{
           name: "",
           icon: "rect"
          },{
           name: "",
           icon: "line"
          }]
         }
                ],
                xAxis: [
                        {
                            type: 'category',
                            data:["","","","","","","","","","","",""],
                            axisTick:false,
                            axisLabel: {
				                interval:0,//
				                rotate:30,//-30    
                            }
                        }
                    ],
                    yAxis: [
                        {
                            axisLine: {show:false},
                            axisTick: {show:false},
                            // max:20,
                            type: 'value',
                            // name: '/ml',
                            min: 0,
                            max: 400,
                            interval: 200,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        },
                        {
                            axisLine: {show:false},//y
                            axisTick: {show:false},//y
                            max:20,
                            type: 'value',
                            // name: '/ml',
                            min: 0,
                            max: 200,
                            interval: 100,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        }
                    ],
                series: [
                        {
                            name:'',
                            type:'bar',
                            yAxisIndex:0,
                            barWidth: '15',
                            itemStyle: {
                                normal: {
                                    color: '#5B9BD5',
                                    /**/
                                    // label: {
                                    //     show: true,
                                    //     position: 'top',
                                    //     // formatter: '{b}\n{c}'
                                    // }
                                }
                            },
                            data:["220","260","0","190","0","0","200","180","210","130","140","205"]
                        },
                        {
                            name:'',
                            type:'line',
                            yAxisIndex:1,
                            showSymbol:false,//
                            symbol:'none',//
                            itemStyle : {  /**/
                                normal : {
                                    color:'#ED833C'
                                }
                            },
                            data:["10","30","25","50","60","40","40","100","80","120","110","90"]
                        }

                    ]
            });
            window.addEventListener("resize", () => { myChart.resize();});
        }
    }
}
</script>
<style scoped>
</style>



