<template>
    <div>
        <!-- <div class="title" style="text-align:center"></div> -->
        <div id="myChart3" :style="{width:'100%',height:'300px',marginTop:'30px'}"></div>
    </div>
</template>
<script>
export default {
    data(){
        return{

        }
    },
    mounted(){
        this.drawLine()
    },
    methods:{
        drawLine(){
            let myChart = this.$echarts.init(document.getElementById('myChart3'))
             myChart.setOption({
                 title:{
                     text:'',
                     left:'center',
                     textStyle:{
                        fontSize:16,
                        fontWeight:'normal'
                    }
                 },
                 tooltip : {
                    trigger: 'axis',
                    axisPointer : {            // 
                        type : 'shadow'        // 'line' | 'shadow'
                    }
                },
                legend: [
                {
       bottom: -3,
                    // right: "24%",
      textStyle: {
       color: "black",
       fontSize: 12,
       fontFamily: ""
      },
      itemWidth: 10,
      itemHeight: 10,
      data:[
                    {
       name: "",
       icon: "rect"
      },{
       name: "",
       icon: "rect"
      }]
     }
                ],
                grid: {
                    left: '6%',
                    right: '6%',
                    bottom: '30',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    data: ['11','12','','14','15','16','17','17','18','19','20','21','22','23','24','25','','','','','','','','','',''],
                    axisTick:false,
                    axisLabel: {
				        interval:0,//
                        rotate:90,//-30    
                        textStyle:{
                            fontSize:10
                        },
                        formatter : function (value) {
                            let valueTxt = '';
                            if (value.length > 3) {
                                valueTxt = value.substring(0, 3) + '...';
                            }else {
                                valueTxt = value;
                            }
                            return valueTxt ;
                        }
                    }
                },
                yAxis:  [
                    {
                        type: 'value',
                        axisLine: {show:false},
                        axisTick: {show:false},
                        min: 0,
                        max: 30,
                        interval: 10
                    },
                    {
                        type: 'value',
                        axisLine: {show:false},
                        axisTick: {show:false},
                        splitLine:{show:false},
                        min: 0,
                        max: 10,
                        interval: 5,
                    }
                ],
                series: [
                    {
                        name: '',
                        type: 'bar',
                        stack: '',
                        barWidth:6,
                        label: {
                            normal: {
                                // show: true
                            }
                        },
                        itemStyle : {  /**/
                            normal : {
                                color:'#4472C4'
                            }
                        },
                        data: [10, 7, 3, 3, 2, 5, 0,5,0,7,0,13,2,5,0,6,7,2,5,15,15,20,17,13,10,10]
                    },
                    {
                        name: '',
                        type: 'bar',
                        stack: '',
                        yAxisIndex:1,
                        barWidth:6,
                        label: {
                            normal: {
                                // show: true
                            }
                        },
                        itemStyle : {  /**/
                            normal : {
                                color:'#ED7D31'
                            }
                        },
                        data: [3, 2, 4, 1, 3, 7, 0,4,0,3,0,0,0,4,0,0.2,0,0,0.1,0,0,2,0,0]
                    }
                ]
             });
             window.addEventListener("resize", () => { myChart.resize();});
        }
    }
}
</script>
<style scoped>

</style>



