<template>
    <div>
        <div class="title" style="text-align:center;margin-top:30px;">
            <span style="font-size:16px;"></span>
            <select id="select">
                <option></option>
                <option></option>
                <option></option>
            </select>
            <select id="select1">
                <option>2019</option>
                <option>2018</option>
                <option>2017</option>
            </select>
        </div>
        <div id="myChart4" :style="{width:'100%',height:'300px'}"></div>
    </div>
</template>
<script>
import $ from 'jquery'
export default {
    data(){
        return{
            
        }
    },
    mounted(){
        this.drawLine();
        $("#select").change(function(){
            $.ajax({
                type : "get",
                data : {
                    'vals':$('#select').val(),
                    'value':$('#select1').val()
                },
                dataType : "json",
                url : "",
                success : function(data) {
                    //data Echarts
                }
            });
            console.log(this.value,$('#select1').val())
        });
        $("#select1").change(function(){
            $.ajax({
                type : "get",
                data : {
                    'vals':$('#select').val(),
                    'value':$('#select1').val()
                },
                dataType : "json",
                url : "",
                success : function(data) {
                    //data Echarts
                }
            });
            console.log($('#select').val(),this.value)
        });
    },
    methods:{
        drawLine(){
            let myChart = this.$echarts.init(document.getElementById('myChart4'))
            myChart.setOption({
                tooltip: {
                    trigger: 'axis',
                    axisPointer : { // 
                        type : 'shadow' // 'line' | 'shadow'
                    }
                },
                grid:{
                    top:'20%',
                    bottom:'20%',
                    left:'13%',
                    right:'6%',
                    // height: '250',
                },
                legend: [
                    {
           bottom: 0,
                        // right: "24%",
          textStyle: {
           color: "black",
           fontSize: 12,
           fontFamily: ""
          },
          itemWidth: 20,
          itemHeight: 7,
          data:[{
           name: "",
           icon: "rect"
          },{
           name: "",
           icon: "line"
          }]
         }
                ],
                xAxis: [
                        {
                            type: 'category',
                            data:["","","",""],
                            axisTick:false,
                            axisLabel: {
				                interval:0,//
				                //rotate:90,//-30    
                            }
                        }
                    ],
                    yAxis: [
                        {
                            axisLine: {show:false},
                            axisTick: {show:false},
                            // max:20,
                            type: 'value',
                            // name: '/ml',
                            min: 0,
                            max: 1000,
                            interval: 500,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        },
                        {
                            axisLine: {show:false},//y
                            axisTick: {show:false},//y
                            // splitLine:{show:false},//
                            max:20,
                            type: 'value',
                            // name: '/ml',
                            min: 0,
                            max: 40,
                            interval: 20,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        }
                    ],
                series: [
                        {
                            name:'',
                            type:'bar',
                            yAxisIndex:0,
                            barWidth: '30',
                            itemStyle: {
                                normal: {
                                    color: '#5B9BD5',
                                    /**/
                                    // label: {
                                    //     show: true,
                                    //     position: 'top',
                                    //     // formatter: '{b}\n{c}'
                                    // }
                                }
                            },
                            data:['500','390','180','480']
                        },
                        {
                            name:'',
                            type:'line',
                            yAxisIndex:1,
                            showSymbol:false,//
                            symbol:'none',//
                            itemStyle : {  /**/
                                normal : {
                                    color:'#ED833C'
                                }
                            },
                            data:['32','15','8','30']
                        }

                    ]
            });
            window.addEventListener("resize", () => { myChart.resize();});
        }
    }
}
</script>
<style scoped>
    #select{
        margin-left: 10px;
        outline: none;
        font-size: 12px;
    }
    #select1{
        outline: none;
        font-size: 12px;
    }
</style>



